//
// _variable.scss
//

// Prefix for :root CSS variables
$prefix: 'bs-';

// Google Font Family link
@import url('https://fonts.googleapis.com/css2?family=Wix+Madefor+Text:ital,wght@0,400..800;1,400..800&display=swap');

// Font Family
$font-family-primary: 'Wix Madefor Text', sans-serif;
$font-family-secondary: 'Wix Madefor Text', sans-serif;

//
// Color system
//

// scss-docs-start gray-color-variables
$white: #ffffff;
$gray-100: #f8f9fa;
$gray-200: #eef2f7;
$gray-300: #d8dfe7;
$gray-400: #b0b0bb;
$gray-500: #8486a7;
$gray-600: #5d7186;
$gray-700: #424e5a;
$gray-800: #36404a;
$gray-900: #323a46;
$black: #000000;
// scss-docs-end gray-color-variables

// scss-docs-start color-variables
$blue: #1c84ee;
$indigo: #53389f;
$purple: #7f56da;
$pink: #ff86c8;
$red: #ef5f5f;
$orange: #ff6c2f;
$yellow: #f9b931;
$green: #22c55e;
$teal: #040505;
$cyan: #4ecac2;
// scss-docs-end color-variables

// scss-docs-start theme-color-variables
$primary: $blue;
$secondary: $gray-600;
$blue: $blue;
$indigo: $indigo;
$purple: $purple;
$pink: $pink;
$danger: $red;
$orange: $orange;
$warning: $yellow;
$success: $green;
$info: $cyan;
$light: $gray-200;
$dark: $gray-900;
// scss-docs-end theme-color-variables

// scss-docs-start gray-colors-map
$grays: (
    '100': $gray-100,
    '200': $gray-200,
    '300': $gray-300,
    '400': $gray-400,
    '500': $gray-500,
    '600': $gray-600,
    '700': $gray-700,
    '800': $gray-800,
    '900': $gray-900,
);
// scss-docs-end gray-colors-map

// scss-docs-start theme-colors-map
$theme-colors: (
    'primary': $primary,
    'secondary': $secondary,
    'success': $success,
    'warning': $warning,
    'info': $info,
    'danger': $danger,
    'purple': $purple,
    'pink': $pink,
    'blue': $blue,
    'orange': $orange,
    'indigo': $indigo,
    'red': $red,
    'yellow': $yellow,
    'green': $green,
    'teal': $teal,
    'cyan': $cyan,
    'light': $light,
    'dark': $dark,
);
// scss-docs-end theme-colors-map

// scss-docs-start colors-map
$colors: (
    'white': $white,
    'black': $black,
    'gray': $gray-600,
    'gray-dark': $gray-800,
);
// scss-docs-end colors-map

// The contrast ratio to reach against white, to determine if color changes from "light" to "dark". Acceptable values for WCAG 2.0 are 3, 4.5 and 7.
// See https://www.w3.org/TR/WCAG20/#visual-audio-contrast-contrast
$min-contrast-ratio: 1.5;

// Customize the light and dark text colors for use in our color contrast function.
$color-contrast-dark: $gray-900;
$color-contrast-light: $white;

